
.categories {
  padding-bottom: 20px;
}
.category-item {
  margin: 5px 20px;
  display: inline-block;
  cursor: pointer;
}
.category-item:hover {
  text-decoration: underline;
}
.category-active {
  color: #ffa500;
}
.news-view {
  padding-top: 10px;
}
.news-list-nav,
.news-list {
  background-color: #fff;
  border-radius: 2px;
}
.news-list-nav {
  padding: 15px 30px;
  position: fixed;
  text-align: center;
  left: 0;
  right: 0;
  z-index: 998;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}
.news-list-nav a {
  margin: 0 1em;
}
.news-list-nav .disabled {
  color: #ccc;
}
.news-list {
  position: absolute;
  margin: 30px 0;
  width: 100%;
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}
.news-list ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
          transform: translate(30px, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
          transform: translate(-30px, 0);
}
.item-move,
.item-enter-active,
.item-leave-active {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}
.item-enter {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
          transform: translate(30px, 0);
}
.item-leave-active {
  position: absolute;
  opacity: 0;
  -webkit-transform: translate(30px, 0);
          transform: translate(30px, 0);
}
@media (max-width: 600px) {
.news-list {
    margin: 10px 0;
}
}
.search-bar input {
  width: 100%;
  margin: 20px 0;
  padding: 10px;
}
/* Filters */
.filters {
  position: relative;
  margin-top: 1em;
}
.filters input {
  width: 100%;
}
.filters button {
  background-color: #f60;
  color: #fff;
  box-shadow: none;
  border: none;
  font-size: 14px;
  height: 30px;
  border-radius: 2px;
}
.active-tags {
  margin-top: 1em;
}
.active-tag {
  padding: 0.5em;
  color: #fff;
  background-color: #f60;
}
.remove-tag-button:hover {
  cursor: pointer;
}
.spinner-holder {
  width: 100%;
  text-align: center;
  margin: 10px;
}
.auto-complete {
  width: 200px;
  background: #fff;
  padding: 1em;
  position: absolute;
  top: 30px;
  z-index: 1000;
  box-shadow: 6px 0px 10px #efefef;
}
.auto-complete .add-tag-button {
  color: #f60;
  font-size: 10px;
}
.auto-complete .add-tag-button:hover {
  cursor: pointer;
}
@media (max-width: 600px) {
.category-item {
    width: 100%;
}
.news-list {
    margin: 10px 0;
}
}


