
.item-view-header {
  background-color: #fff;
  padding: 1.8em 2em 1em;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}
.item-view-header h1 {
  display: inline;
  font-size: 1.5em;
  margin: 0;
  margin-right: 0.5em;
}
.item-view-header .host,
.item-view-header .meta,
.item-view-header .meta a {
  color: #999;
}
.item-view-header .meta a {
  text-decoration: underline;
}
.item-view-header .arrow {
  color: #888;
}
.item-view-header .arrow:hover {
  cursor: pointer;
  color: #f60;
}
.item-view-header .arrow.active {
  color: #f60 !important;
}
.item-view-header .arrow.active:hover {
  cursor: pointer;
  color: #888;
}
.item-view-comments {
  background-color: #fff;
  margin-top: 10px;
  padding: 1em 2em 0.5em;
}
.item-view-comments .row,
.item-view-comments h2 {
  display: none;
}
.item-view-comments-header {
  margin: 0;
  font-size: 1.1em;
  padding: 1em 0;
  position: relative;
}
.item-view-comments-header .spinner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: auto;
}
.comment-children {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 600px) {
.item-view-header h1 {
    font-size: 1.25em;
}
}


