
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 15px;
  background-color: #f2f3f5 !important;
  margin: 0;
  color: #34495e;
  overflow-y: scroll;
}
body #app {
  margin-bottom: 120px;
}
a {
  color: #34495e;
  text-decoration: none;
}
.header {
  background-color: #f60;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
}
.header .inner {
  max-width: 800px;
  box-sizing: border-box;
  margin: 0px auto;
  padding: 15px 5px;
}
.header a {
  color: rgba(255,255,255,0.8);
  line-height: 24px;
  transition: color 0.15s ease;
  display: inline-block;
  vertical-align: middle;
  font-weight: 300;
  letter-spacing: 0.075em;
  margin-right: 1.8em;
}
.header a:hover {
  color: #fff;
}
.header a.router-link-active {
  color: #fff;
  font-weight: 400;
}
.header a:nth-child(6) {
  margin-right: 0;
}
.header .github {
  color: #fff;
  font-size: 0.9em;
  margin: 0;
  float: right;
}
.player-holder {
  padding: 10px 20px;
  box-shadow: -5px -5px 5px -2px rgba(176,176,176,0.3);
  background-color: #fff;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}
.logo {
  width: 30px;
  margin-right: 10px;
  display: inline-block;
  vertical-align: middle;
}
.view {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
@media (max-width: 860px) {
.header .inner {
    padding: 15px 30px;
}
}
@media (max-width: 600px) {
body {
    font-size: 14px;
}
.header {
    min-height: 80px;
}
.header .inner {
    padding: 15px;
}
.header a {
    margin-right: 1em;
}
.header .github {
    display: none;
}
}


